.cart-item {
    display: block;
    align-items: center;
    border-bottom: 2px solid #004687;
    padding: 1rem 0;
    /* margin: 1rem 0; */
  }
  
  .cart-item h2 {
    margin: 0 0 0.5rem 0;
    color: #363636;
    text-align: right;
  }
  
  .summary {
    width: 80%;
    display: inline-block;
  }
  
  .price {
    font-weight: bold;
    color: #72a0c1;
  }
  
  .amount {
    margin: 0 0 0 20px;
    font-weight: bold;
    border: 1px solid #ccc;
    padding: 0.25rem 0.75rem;
    border-radius: 6px;
    color: #363636;
  }
  
  .actions {
    display: inline-block;
  }
  
  .cart-item button {
    font: inherit;
    font-weight: bold;
    font-size: 1.25rem;
    color: #72a0c1;
    border: 1px solid #72a0c1;
    width: 3rem;
    text-align: center;
    border-radius: 6px;
    background-color: transparent;
    cursor: pointer;
    margin-left: 1rem;
    margin: 0.25rem;
  }
  
  .cart-item button:hover,
  .cart-item button:active {
    background-color: #72a0c1;
    color: white;
  }